@import url('https://fonts.googleapis.com/css?family=Lato:300,400|Poppins:300,400,800&display=swap');


body {
  margin: 0;
  padding: 0;
  overflow: auto;
  background: #ffffff;
}

.parent-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #ffffff;
}

.container {
  margin-top: 10%;
  width: 100%;
  height: auto;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}
  
  .container .box {
  width: 250px;
  height: 250px;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  }
  
  .container .box .title {
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  height: 50px;
  }
  
  .container .box .title .block {
  width: 0%;
  height: inherit;
  background: #0078FF;
  position: absolute;
  animation: mainBlock 2s cubic-bezier(.74, .06, .4, .92) forwards;
  display: flex;
  }
  
  .container .box  .title  h1 {
  font-family: 'Poppins';
  color: #000000;
  font-size: 38px;
  -webkit-animation: mainFadeIn 2s forwards;
  -o-animation: mainFadeIn 2s forwards;
  animation: mainFadeIn 2s forwards;
  animation-delay: 1.6s;
  opacity: 0;
  display: flex;
  align-items: baseline;
  position: relative;
  }
  
  .container .box .title h1 span {
  width:0px;
  height: 0px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  background: #ffb510;
  -webkit-animation: load 0.6s cubic-bezier(.74, .06, .4, .92) forwards;
  animation: popIn 0.8s cubic-bezier(.74, .06, .4, .92) forwards;
  animation-delay: 2s;
  margin-left: 5px;
  margin-top: -10px;
  position: absolute;
  bottom: 13px;
  right: -12px;
  }
  
  .container .box .role {
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  height: 30px;
  margin-top: -10px;
  }
  
  .container .box .role .block {
  width: 0%;
  height: inherit;
  background: #0078FF;
  position: absolute;
  animation: secBlock 2s cubic-bezier(.74, .06, .4, .92) forwards;
  animation-delay: 2s;
  display: flex;
  }
  
  .container .box .role p {
  animation: secFadeIn 2s forwards;
  animation-delay: 3.2s;
  opacity: 0;
  font-weight: 400;
  font-family: 'Lato';
  color: #000000;
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 5px;
  }

.sync-files {
  font-family: 'Lato';
  font-size: 12px;
  opacity: 0.5;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 400;
  color: #000000;
  display: flex;
  align-items: center;
  justify-content: center;
}

@keyframes mainBlock {
0% {
  width: 0%;
  left: 0;

}
50% {
  width: 100%;
  left: 0;

}
100% {
  width: 0;
  left: 100%;
}
}

@keyframes secBlock {
0% {
  width: 0%;
  left: 0;

}
50% {
  width: 100%;
  left: 0;

}
100% {
  width: 0;
  left: 100%;
}
}

@keyframes mainFadeIn {
0% {
  opacity: 0;
}
100% {
  opacity: 1;
}
}


@keyframes secFadeIn {
0% {
  opacity: 0;
}
100% {
  opacity: 0.5;
}
}




footer {
  width: 350px;
  height: 80px;
  background: #0078FF;
  position: absolute;
  right: 0;
  bottom: -80px;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: top 0.8s forwards;
  animation-delay: 4s;
}

footer > span {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  color: #ffffff;
  font-family: 'Poppins';
}

footer > span > i {
  margin-right: 25px;
  font-size: 22px;
  color: #ffffff;
  animation: icon 2s forwards;
  animation-delay: 4s;
  opacity: 0;
}

@keyframes top {
0% {
  opacity: 0;
   bottom: -80px
}
100% {
  opacity: 1;
   bottom: 0px

}
}

@keyframes icon {
0% {
  opacity: 0;
   transform: scale(0.0);
}
 50% {
    opacity: 1;
   transform: scale(1.3) rotate(-02deg);
 }
100% {
  opacity: 1;
   bottom: 0px;
}
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes resetOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeInOut {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}